import React from 'react'
import styled from 'styled-components'
import './main.css'

class App extends React.Component{
    state = {
        color:'',
        author: '',
        quote:''
    }
    
    getRandomColor() {
        var letters = '0123456789ABCDEF';
        var color = '#';
        for (var i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
      }
      fetchdata(){
          return new Promise ((resolve,reject)=>{
              fetch('https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json')
              .then(data=>data.json())
              .then(data=>resolve(data))
          })
      }

    changeColor = ()=>{
        let randomNumber =  Math.floor(Math.random() * (101 - 1) + 1)
        this.fetchdata()
        .then (data=>{
            this.setState({
                quote:data.quotes[randomNumber].quote,
                author:data.quotes[randomNumber].author
            })
        })
        let randomColor = () =>{
            var letters = '0123456789ABCDEF';
        var color = '#';
        for (var i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)];
        }

        return color;
        }
        this.setState({
            color:randomColor()
        



        })
        
    }
    
    componentDidMount() {
        this.changeColor()
      
    }
    render(){
        
        return(
            <div style={{background: this.state.color}} className="background">
            <div className="quote-box">
                <div className="phraseBox">
                    <i className="fas fa-quote-left"></i>
                    <span id="text">{this.state.quote}</span>
                </div>
                <div id="authorBox">
                    <span id="author"> - {this.state.author}</span>
                </div>
                <div className="buttonsBox">
                <a
                    className="button"
                    id="tweet-quote"
                    title="Tweet this quote!"
                    target="_top"
                    href={`https://twitter.com/intent/tweet?text="${this.state.quote}${this.state.author}"`}>
                        
                    <i className="fab fa-twitter-square"></i>
                

                
      </a>

                
                    <button id="changeButton"
                    onClick={this.changeColor}
                    >cambiar</button>
                    </div>
                </div>
            
                
            </div>
        )
    }
}
export default App;
